import { memo, useCallback, useMemo, useState } from 'react'
import './TournamentMenu.css'
import SmoothCollapse from 'react-smooth-collapse'
import { useSelector, useDispatch } from 'react-redux'
import { useParams } from 'react-router'

import PlayersGrey from '../../icon/players-grey.svg'
import PlayersBlue from '../../icon/players-blue.svg'
import StructureGrey from '../../icon/structure_menu.svg'
import StructureBlue from '../../icon/structure_menu_blue.svg'
import StandingGrey from '../../icon/grids_menu.svg'
import StandingBlue from '../../icon/grids_menu_blue.svg'
import ScheduleGrey from '../../icon/sched-grey.svg'
import ScheduleBlue from '../../icon/sched-blue.svg'
import { ReactComponent as ExitGrey } from '../../icon/exit-grey.svg'
import { ReactComponent as ExitBlue } from '../../icon/exit-blue.svg'
import { ReactComponent as InfoGrey } from '../../icon/info-grey.svg'
import { ReactComponent as InfoBlue } from '../../icon/info-blue.svg'
import { ReactComponent as ArrowDown } from '../../icon/arrow-down.svg'
import { ReactComponent as CupGrey } from '../../icon/cup.svg'
import { ReactComponent as CupBlue } from '../../icon/cup_blue.svg'
import { createStage } from '../../redux/singleTournamentService/singleTournamentSlice'
import { selectStageCreation } from '../../redux/selectors'

function TournamentMenuComponent({
	stages, menuTabOrder, handleStageTab, stageTabOrder, handleMenuTab
}) {
	const dispatch = useDispatch()
	const { guest } = useParams()
	const stageCreation = useSelector(selectStageCreation)

	const [showMainMenu, setShowMainMenu] = useState(true)

	const shownStage = useMemo(() => {
		if (menuTabOrder < 4) {
			return true
		} else {
			return false
		}
	}, [menuTabOrder])

	const handleAddStage = useCallback(() => {
		dispatch(createStage(true))
		sessionStorage.setItem('activatedComponent', 'createStage')
	}, [dispatch])

	return (
		<>
			<div className="tournament-footer__container">
				{
					shownStage &&
					<div className="tournament__stages-menu">
						{
							stages?.length > 0 &&
							<div className="stages-menu__item">
								{
									menuTabOrder === 1 &&
									<img
										src={StandingBlue}
										className="stages-menu__item-icon"
										alt="Standing"
									/>
								}

								{
									menuTabOrder === 2 &&
									<img
										src={ScheduleBlue}
										className="stages-menu__item-icon"
										alt="Schedule"
									/>
								}

								{
									menuTabOrder === 3 &&
									<img
										src={StructureBlue}
										className="stages-menu__item-icon"
										alt="Structure"
									/>
								}

								{
									stages?.map((s, index) => (
										<button
											type="button"
											key={index}
											onClick={() => handleStageTab(index + 1)}
											className={
												stageTabOrder === index + 1 &&
													!stageCreation ?
													'tournament__stages-tab--chosen'
													:
													'tournament__stages-tab'
											}
										>
											{index + 1}
										</button>
									))
								}

								{
									menuTabOrder === 3 && stages?.length < 2 &&
									<button
										type="button"
										onClick={handleAddStage}
										className={
											stageCreation ?
												'tournament__stage-addition--active'
												:
												'button-stage-addition'
										}
									>
										+
									</button>
								}

								<ArrowDown
									className={showMainMenu ?
										'arrow--down'
										:
										'arrow--up'
									}
									onClick={() => setShowMainMenu(prev => !prev)}
								/>
							</div>
						}

						{
							stages?.length === 0 && menuTabOrder === 3 &&
							<div className="tournament__stage-menu">
								<img
									src={StructureBlue}
									className="stages-menu__item-icon"
									alt="Structure"
								/>

								<button
									type="button"
									onClick={handleAddStage}
									className="tournament__stage-addition--active"
								>
									+
								</button>

								<ArrowDown
									className={showMainMenu ?
										'arrow--down'
										:
										'arrow--up'
									}
									onClick={() => setShowMainMenu(prev => !prev)}
								/>
							</div>
						}
					</div>
				}

				<SmoothCollapse expanded={showMainMenu}>
					<div className={menuTabOrder > 3 || (menuTabOrder < 3 && stages?.length < 1) ?
						'tournament__footer-menu'
						:
						'tournament__footer-menu--opened'
					}>
						<div onClick={() => handleMenuTab(1)} className={'tournament-footer__link'}>
							{
								menuTabOrder === 1 ?
									<img
										src={StandingBlue}
										style={{ height: '27px', width: '33px' }}
										alt="Standing"
									/>
									:
									<img
										src={StandingGrey}
										style={{ height: '27px', width: '33px' }}
										alt="Standing"
									/>
							}
						</div>

						<div onClick={() => handleMenuTab(2)} className={'tournament-footer__link'}>
							{
								menuTabOrder === 2 ?
									<img
										src={ScheduleBlue}
										style={{ height: '32px', width: '31px' }}
										alt="Schedule"
									/>
									:
									<img
										src={ScheduleGrey}
										style={{ height: '32px', width: '31px' }}
										alt="Schedule"
									/>
							}
						</div>

						{!guest &&
							<div onClick={() => handleMenuTab(3)} className={'tournament-footer__link'}>
								{
									menuTabOrder === 3 ?
										<img
											src={StructureBlue}
											style={{ height: '30px', width: '38px' }}
											alt="Structure"
										/>
										:
										<img
											src={StructureGrey}
											style={{ height: '30px', width: '38px' }}
											alt="Structure"
										/>
								}
							</div>
						}

						<div onClick={() => handleMenuTab(4)} className={'tournament-footer__link'}>
							{
								menuTabOrder === 4 ?
									<img
										src={PlayersBlue}
										style={{ height: '30px', width: '30px' }}
										alt="Players"
									/>
									:
									<img
										src={PlayersGrey}
										style={{ height: '30px', width: '30px' }}
										alt="Players"
									/>
							}
						</div>

						<div onClick={() => handleMenuTab(6)} className={'tournament-footer__link'}>
							{
								menuTabOrder === 6 ?
									<CupBlue className="cup-icon" />
									:
									<CupGrey className="cup-icon" />
							}
						</div>

						<div onClick={() => handleMenuTab(5)} className={'tournament-footer__link'}>
							{
								menuTabOrder === 5 ?
									<InfoBlue className="footer-icon" />
									:
									<InfoGrey className="footer-icon" />
							}
						</div>

						<div onClick={() => handleMenuTab(7)} className={'tournament-footer__link'}>
							{
								menuTabOrder === 7 ?
									<ExitBlue className="footer-icon" />
									:
									<ExitGrey className="footer-icon" />
							}
						</div>
					</div>
				</SmoothCollapse>
			</div>
		</>
	)
}

export const TournamentMenu = memo(TournamentMenuComponent)