import './Match.css'
import { useEffect, useState, memo, useMemo, useCallback } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'

import { useSaveSetMutation, useUpdateMatchMutation } from '../../redux/matchService/matchApiSlice'
import { TYPE_SINGLES } from '../../general/generalConstants'
import { selectTournament } from '../../redux/selectors'
import { ReactComponent as Minus } from '../../icon/minus-delete.svg'
import { ReactComponent as Plus } from '../../icon/plus-blue.svg'
import { ButtonWhite } from '../../components/ButtonWhite/ButtonWhite'
import { ReactComponent as Cross } from '../../icon/close.svg'

const getMatch = (match, matchSession) => {
	if ((match && Object.keys(match).length !== 0) || Object.keys(matchSession).length !== 0) {
		const currentMatch = JSON.parse(matchSession)
		return currentMatch
	}
}

function MatchComponent({ match, closeMatch }) {
	const { t } = useTranslation()
	const matchSession = sessionStorage.getItem('match')
	const [updateMatch] = useUpdateMatchMutation()
	const [saveSet] = useSaveSetMutation()
	const { type } = useSelector(selectTournament)

	const [scoresSideOne, setScoresSideOne] = useState(0)
	const [scoresSideTwo, setScoresSideTwo] = useState(0)
	const [tiebreakScoresSideOne, setTiebreakScoresSideOne] = useState(0)
	const [tiebreakScoresSideTwo, setTiebreakScoresSideTwo] = useState(0)
	const [tiebreak, setTiebreak] = useState(false)
	const [matchState, setMatchState] = useState(getMatch(match, matchSession))

	console.log(match)

	const firstParticipant = useMemo(() => {
		if (match?.side1_uid === match?.side1?.nodeUid) {
			return match?.side1
		} else if (match?.side1_uid === match?.side2?.nodeUid) {
			return match?.side2
		}
	}, [match])

	const secondParticipant = useMemo(() => {
		if (match?.side2_uid === match?.side2?.nodeUid) {
			return match?.side2
		} else if (match?.side2_uid === match?.side1?.nodeUid) {
			return match?.side1
		}
	}, [match])

	useEffect(() => {
		if (matchState?.sets?.length > 0) {
			const setsCount = matchState?.sets?.length - 1
			const firstTiebreak = matchState?.sets[setsCount]?.tie_break_score1
			const secondTiebreak = matchState?.sets[setsCount]?.tie_break_score2

			setScoresSideOne(matchState?.sets[setsCount]?.score1)
			setScoresSideTwo(matchState?.sets[setsCount]?.score2)
			setTiebreakScoresSideOne(firstTiebreak ? firstTiebreak : 0)
			setTiebreakScoresSideTwo(secondTiebreak ? secondTiebreak : 0)
		}
	}, [matchState])

	const handlePlus = useCallback((player) => {
		if (player === 1) {
			if (!tiebreak) {
				setScoresSideOne(prev => prev + 1)
			} else if (tiebreak) {
				setTiebreakScoresSideOne(prev => prev + 1)
			}

		} else if (player === 2) {
			if (!tiebreak) {
				setScoresSideTwo(prev => prev + 1)
			} else if (tiebreak) {
				setTiebreakScoresSideTwo(prev => prev + 1)
			}
		}
	}, [tiebreak])

	const handleMinus = useCallback((player) => {
		if (player === 1) {
			if (!tiebreak && scoresSideOne > 0) {
				setScoresSideOne(prev => prev - 1)
			} else if (tiebreak && tiebreakScoresSideOne > 0) {
				setTiebreakScoresSideOne(prev => prev - 1)
			}

		} else if (player === 2) {
			if (!tiebreak && scoresSideTwo > 0) {
				setScoresSideTwo(prev => prev - 1)
			} else if (tiebreak && tiebreakScoresSideTwo > 0) {
				setTiebreakScoresSideTwo(prev => prev - 1)
			}
		}
	}, [tiebreak, tiebreakScoresSideOne, tiebreakScoresSideTwo, scoresSideTwo, scoresSideOne])

	const addSix = useCallback((player) => {
		if (player === 1) {
			if (!tiebreak) {
				setScoresSideOne(6)
			} else {
				setTiebreakScoresSideOne(6)
			}
		} else {
			if (!tiebreak) {
				setScoresSideTwo(6)
			} else {
				setTiebreakScoresSideTwo(6)
			}
		}
	}, [tiebreak])

	const activateTiebreak = useCallback(() => {
		setTiebreak(prev => !prev)
	}, [])

	const handleAddSet = async () => {
		const matchData = { ...JSON.parse(matchSession) }
		const newSet = {
			'order_number': matchState?.sets?.length + 1,
			'score1': 0,
			'tie_break_score1': null,
			'score2': 0,
			'tie_break_score2': null
		}

		const sets = [...matchData?.sets, newSet]
		const body = {
			'is_finished': false,
			'sets': sets
		}

		matchData?.sets?.push(newSet)
		sessionStorage.setItem('match', JSON.stringify(matchData))
		try {
			const response = await saveSet({ uid: matchState?.uid, body })

			if (!response?.error) {
				setMatchState({ ...matchState, sets: response?.data?.sets })
				setScoresSideOne(0)
				setScoresSideTwo(0)
				setTiebreakScoresSideOne(0)
				setTiebreakScoresSideTwo(0)
				setTiebreak(false)
			}
		}
		catch (e) {
			console.log('handleRemoveSet catched error', e)
		}
	}

	const handleRemoveSet = async () => {
		const matchData = { ...JSON.parse(matchSession) }
		const sets = [...matchState?.sets]
		sets?.pop()
		const body = {
			'is_finished': false,
			'sets': sets
		}

		matchData?.sets?.pop()
		sessionStorage.setItem('match', JSON.stringify(matchData))

		try {
			const response = await saveSet({ uid: matchState?.uid, body })

			if (!response?.error) {
				if (response?.data?.sets?.length > 0) {
					setMatchState({ ...matchState, sets: response?.data?.sets })
				} else {
					setScoresSideOne(0)
					setScoresSideTwo(0)
					setTiebreakScoresSideOne(0)
					setTiebreakScoresSideTwo(0)
				}

				setTiebreak(false)
			}
		}
		catch (e) {
			console.log('handleRemoveSet catched error', e)
		}
	}

	const defineTiebreakForPlayer = useCallback((side) => {
		if (tiebreakScoresSideOne === 0 && tiebreakScoresSideTwo === 0) {
			return null
		} else if (side === 1 && tiebreakScoresSideOne === 0 && tiebreakScoresSideTwo !== 0) {
			return 0
		} else if (side === 2 && tiebreakScoresSideOne !== 0 && tiebreakScoresSideTwo === 0) {
			return 0
		} else if (side === 1) {
			return tiebreakScoresSideOne
		} else {
			return tiebreakScoresSideTwo
		}
	}, [tiebreakScoresSideOne, tiebreakScoresSideTwo])

	const updateLastSet = useCallback(() => {
		const sets = matchState?.sets
		const updatedSet = {
			'order_number': sets?.length + 1,
			'score1': scoresSideOne,
			'tie_break_score1': defineTiebreakForPlayer(1),
			'score2': scoresSideTwo,
			'tie_break_score2': defineTiebreakForPlayer(2)
		}

		const updatedSets = sets?.length > 0 ? [...sets.slice(0, -1), updatedSet] : [updatedSet]
		return updatedSets
	}, [scoresSideOne, scoresSideTwo, defineTiebreakForPlayer, matchState])

	const handleSaveSet = useCallback(async () => {
		const matchData = { ...JSON.parse(matchSession) }
		const sets = updateLastSet()

		matchData.sets = sets
		sessionStorage.setItem('match', JSON.stringify(matchData))

		const body = {
			'is_finished': false,
			'sets': sets
		}

		try {
			const response = await saveSet({ uid: matchState?.uid, body })

			if (!response?.error) {
				setMatchState({ ...matchState, sets: response?.data?.sets })
			}
		}
		catch (e) {
			console.log('handleSaveSet catched error', e)
		}
	}, [matchState, updateLastSet, saveSet, matchSession])

	const handleFinishMatch = async () => {
		const sets = updateLastSet()

		const body = {
			'is_finished': sets?.length > 0 ? true : false,
			'sets': sets
		}

		try {
			const response = await updateMatch({ uid: matchState?.uid, body })

			if (!response?.error) {
				closeMatch()
				sessionStorage.removeItem('activatedComponent')
				sessionStorage.removeItem('match')
			}
		} catch (e) {
			console.log('updateMatch catched error', e)
		}
	}

	const handleCloseMatch = useCallback(() => {
		setScoresSideOne(0)
		setScoresSideTwo(0)
		setTiebreakScoresSideOne(0)
		setTiebreakScoresSideTwo(0)
		setTiebreak(false)
		sessionStorage.removeItem('match')
		sessionStorage.removeItem('activatedComponent')
		closeMatch()
	}, [closeMatch])

	return (
		<>
			<div className="match__wrapper">
				<div className="match__container">
					<div className="match__icon-cross">
						<Cross onClick={handleCloseMatch} />
					</div>

					<div className="match__set">
						<div className="match__set-score">
							<div className="match__set-score-players">
								<div className="match__set-players__score">
									<div className="match__set-players__score--current">
										<div className="match__set-players__names">
											<p className="match__set-players__name--first">
												{
													type === TYPE_SINGLES ?
														firstParticipant?.namePlayer
														:
														`${firstParticipant?.namePlayer1} / ${firstParticipant?.namePlayer2}`
												}
											</p>

											<p className="match__set-players__name--second">
												{
													type === TYPE_SINGLES ?
														secondParticipant?.namePlayer
														:
														`${secondParticipant?.namePlayer1} / ${secondParticipant?.namePlayer2}`
												}
											</p>
										</div>

										<div className="match__set-players__scores">
											<p
												className="match__set-players__scores-item--first"
												style={{
													color: tiebreak ? '#CCCCCC' : '#3A3A3A'
												}}
											>
												{scoresSideOne}

												{
													(tiebreak || tiebreakScoresSideOne > 0
														|| tiebreakScoresSideTwo > 0) &&
													<sup
														className="match__set-players__scores-item--extra"
														style={{
															color: tiebreak ? '#3A3A3A' : '#CCCCCC'
														}}
													>
														{tiebreakScoresSideOne}
													</sup>
												}
											</p>

											<p
												className="match__set-players__scores-item--second"
												style={{
													color: tiebreak ? '#CCCCCC' : '#3A3A3A'
												}}
											>
												{scoresSideTwo}

												{
													(tiebreak || tiebreakScoresSideTwo > 0
														|| tiebreakScoresSideOne > 0) &&
													<sup
														className="match__set-players__scores-item--extra"
														style={{
															color: tiebreak ? '#3A3A3A' : '#CCCCCC'
														}}
													>
														{tiebreakScoresSideTwo}
													</sup>
												}
											</p>
										</div>
									</div>
								</div>

								<div className="match__set-players__buttons">
									<div className="match__set-players__buttons-item">
										<button
											type="button"
											onClick={() => handleMinus(1)}
											className="match__set-players__buttons-minus"
										>
											<Minus className="match__set-players__icon-minus" />
										</button>

										<button
											type="button"
											onClick={() => handlePlus(1)}
											className="match__set-players__buttons-plus"
										>
											<Plus />
										</button>
									</div>

									<div className="match__set-players__buttons-item">
										<button
											type="button"
											onClick={() => handleMinus(2)}
											className="match__set-players__buttons-minus"
										>
											<Minus className="match__set-players__icon-minus" />
										</button>

										<button
											type="button"
											onClick={() => handlePlus(2)}
											className="match__set-players__buttons-plus"
										>
											<Plus />
										</button>
									</div>
								</div>
							</div>

							<div className="match__set-buttons">
								<button
									type="button"
									onClick={() => addSix(1)}
									className="match__set-button--six"
								>
									6
								</button>

								<button
									type="button"
									onClick={() => addSix(2)}
									className="match__set-button--six"
								>
									6
								</button>
							</div>

							<button
								className={tiebreak ? 'match__set-taibreak--active' : 'match__set-taibreak'}
								onClick={activateTiebreak}
							>
								{t('Taibreak')}
							</button>

							<ButtonWhite type="button" onClick={handleSaveSet} title={t('Save score')} />
						</div>
					</div>

					<div className="match__sets">
						<div className="match__sets-table">
							{
								matchState?.sets?.length !== 0 ?
									matchState?.sets?.map((set, index) =>
										<div
											style={{
												backgroundColor: set?.isActive ? '#EBF4FF' : 'transparent'
											}}
											key={index}
											className="match__sets-table__sets"
										>
											<p className="match__sets-table__sets-scores--first">
												{set?.score1}
												<sup>
													{set?.tie_break_score1 >= 0 ? set?.tie_break_score1 : ''}
												</sup>
											</p>

											<p className="match__sets-table__sets-scores--second">
												{set?.score2}
												<sup>
													{set?.tie_break_score2 >= 0 ? set?.tie_break_score2 : ''}
												</sup>
											</p>
										</div>
									)
									:
									<div
										style={{
											backgroundColor: '#EBF4FF'
										}}
										className="match__sets-table__sets"
									>
										<p className="match__sets-table__sets-scores--first">
											0
										</p>

										<p className="match__sets-table__sets-scores--second">
											0
										</p>
									</div>
							}

							<div className="match__sets-table__buttons">
								<button
									type="button"
									onClick={handleAddSet}
									className="match__sets-table__buttons-set-addition"
								>
									{t('Add set')}
								</button>

								<button
									type="button"
									onClick={handleRemoveSet}
									className="match__sets-table__buttons-set-deletion"
								>
									{t('Remove set')}
								</button>
							</div>

							<button
								onClick={handleFinishMatch}
								className="match__sets-table__button-finish"
							>
								{t('Finish match')}
							</button>
						</div>
					</div>
				</div>
			</div>
		</>
	)
}

export const Match = memo(MatchComponent)