import { TYPE_SINGLES } from '../../general/generalConstants'

export const formArraysForRequests = (participants, immutableState, type) => {
	const added = []
	const removed = []

	const state = new Map()
	immutableState?.map(item => state.set(item?.uid, item))

	participants?.forEach(p => {
		const foundPlayer = state.get(p?.uid)

		if (!foundPlayer) {
			if (type === TYPE_SINGLES) {
				added.push(p?.uid)
			} else {
				added.push({ player1: p.player1?.uid, player2: p.player2?.uid })
			}
		}
	})

	immutableState?.forEach(p => {
		const foundParticipant = participants?.find(item => p?.uid === item?.uid)

		if (!foundParticipant) {
			removed.push(p?.uid)
		}
	})

	return {
		added,
		removed
	}
}

export const checkParticipantsOrder = (participants, immutableState) => {
	const orderIsChanged = (
		immutableState?.length > 0 &&
		immutableState.some((state, i) => state?.uid !== participants[i]?.uid)
	)

	const seed = orderIsChanged ? participants.map(p => p?.uid) : []

	return seed
}

export const generateRandomUid = () => {
	const randomNumber = Math.floor(Math.random() * 100000).toString()
	const randomLetter = String.fromCharCode(65 + Math.floor(Math.random() * 26))
	const uid = `${randomLetter}${randomNumber}`
	return uid
}

