import { createSlice } from '@reduxjs/toolkit'

const tournamentSlice = createSlice({
	name: 'tournament',
	initialState: {
		tournament: {},
		stageCreation: false,
		match: false
	},
	reducers: {
		setTournamentData(state, action) {
			state.tournament = action.payload
		},
		createStage(state, action) {
			state.stageCreation = action.payload
		},
		openMatch(state, action) {
			state.match = action.payload
		}
	}
})

export const { setTournamentData, createStage, openMatch } = tournamentSlice.actions
export default tournamentSlice.reducer