import './TournamentInfo.css'
import { useTranslation } from 'react-i18next'
import { useState, memo, useMemo } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useSelector } from 'react-redux'

import { ReactComponent as Edition } from '../../icon/edit_blue.svg'
import { ReactComponent as Close } from '../../icon/close.svg'
import { TournamentForm } from '../TournamentForm/TournamentForm'
import { useRemoveTournamentMutation } from '../../redux/communityService/communityApiSlice'
import { GuestLink } from '../../components/GuestTournamentLink/GuestTournamentLink'
import { TYPE_DOUBLES, TYPE_SINGLES } from '../../general/generalConstants'
import { selectTournament } from '../../redux/selectors'

const TournamentInfoComponent = ({ community, auth }) => {
	const { t } = useTranslation()
	const [openedEdition, setOpenedEdition] = useState(false)
	const { tournamentUid } = useParams()
	const navigate = useNavigate()
	const [removeTournamet] = useRemoveTournamentMutation()
	const {
		venue_uid,
		venue,
		start_date,
		type,
		description,
		round_robin_ranking_system,
		auth_code
	} = useSelector(selectTournament)

	const closeEditorComponent = () => {
		setOpenedEdition(false)
	}

	const tournamentVenue = useMemo(() => {
		if (Object.keys(community).length !== 0) {
			const tournamentVenues = community?.venues?.find(venue => {
				return venue.uid === venue_uid
			})
			return {
				label: tournamentVenues?.name,
				value: tournamentVenues?.uid,
				address_string: tournamentVenues?.address_string
			}
		} else {
			return { address_string: venue?.address_string }
		}
	}, [venue_uid, venue, community])

	const guestUrl = useMemo(() => {
		return `${window.location.origin}/ten/#/${tournamentUid}/g/${auth_code}`
	}, [tournamentUid, auth_code])

	// Конвертер даты
	const tournamentDate = useMemo(() => {
		// Разбиваем строку на компоненты (год, месяц, день)
		if (start_date) {
			const [year, month, day] = start_date.split('-')

			// Преобразуем компоненты в формат "dd.mm.yyyy"
			const formattedDate = `${day}.${month}.${year}`

			return formattedDate
		}
	}, [start_date])

	const deleteTournamet = async () => {
		try {
			const response = await removeTournamet({ tournament_uid: tournamentUid }).unwrap()

			navigate(-1)
		} catch (e) {
			console.log('deleteTournamet catch error', e)
		}
	}

	return (
		<>
			<div className="tournament-info">
				{
					auth && !openedEdition ?
						<GuestLink link={guestUrl} />
						:
						null
				}

				<div className="tournament-info__header">
					<p className="tournament-info__header-title">
						{t('Information')}
					</p>

					{
						!openedEdition && auth ?
							<div>
								<Edition
									className="tournament-info__header-edition"
									onClick={() => setOpenedEdition(true)}
								/>
							</div>
							:
							auth ?
								<Close className="close-icon" onClick={() => setOpenedEdition(false)} />
								:
								''
					}
				</div>

				{
					openedEdition && auth ?
						<TournamentForm
							header={false}
							openedEdition={openedEdition}
							closeEditorComponent={closeEditorComponent}
							edit={true}
						/>
						:
						<div className="form-description__container">
							{
								tournamentVenue?.address_string ?
									<p className="form-description__text">
										{tournamentVenue?.address_string}
									</p>
									:
									null
							}

							<p className="form-description__text">
								<span className="form-description__bold">
									{t('Start date')}:
								</span>

								<span> </span>

								{
									tournamentDate
								}
							</p>

							{/*
							<p className="form-description__text">
								<span className="form-description__bold">
									{ t('Tournament category') }:
								</span>
								
								{
									tournamentCategory?.label ?
										tournamentCategory?.label
										:
										t('Category not selected')
								}
							</p>
							*/}

							<p className="form-description__text">
								<span className="form-description__bold">
									{t('Tournament type')}:
								</span>

								<span> </span>

								{
									type === TYPE_SINGLES ?
										t('Single')
										:
										type === TYPE_DOUBLES &&
										t('Double')
								}
							</p>

							{/*
							<p className="form-description__text">
								<span className="form-description__bold">
									{ t('Floor') }:
								</span>
								
								{
									props?.tournament?.gender === 'men' ?
										t('Male')
										:
										props?.tournament?.gender === 'women' ?
											t('Female')
											:
											props?.tournament?.gender === 'mixed' &&
												t('Mix')
								}
							</p>
							*/}

							<p className="form-description__text">
								{
									description ?
										description
										:
										t('No description')
								}
							</p>

							<p className="form-description__text">
								<span className="form-description__bold">
									{t('A method for determining places in a round-robin group when several participants have equal points')}:
								</span>

								<span> </span>

								{
									round_robin_ranking_system === 'group_sets_games_win_percentage' ?
										t('By the percentage of all sets/games won')
										:
										round_robin_ranking_system === 'equal_match_points_sets_games_win_percentage' &&
										t('By the percentage of sets/games won in games with each other')
								}
							</p>
						</div>
				}

				{
					auth &&
					<div className="delete-tournamet__container">
						<button className="delete-tournamet__button" onClick={() => deleteTournamet()}>
							{t('Remove tournament')}
						</button>
					</div>
				}
			</div>
		</>
	)
}

export const TournamentInfo = memo(TournamentInfoComponent)
