import './TournamentStanding.css'
import React, { useEffect, useState, useCallback, memo } from 'react'
import { useParams } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { useSelector, useDispatch } from 'react-redux'

import { useGetStageQuery } from '../../redux/stageService/stageApiSlice'
import { selectTournament } from '../../redux/selectors'
import { assignAlphabeticalNames } from '../../general/generalExternalFunctions'
import { GROUP_TYPE_RR } from '../../general/generalConstants'
import RoundRobinGrid from '../RoundRobinGrid/RoundRobinGrid'
import OlympicPlusGrid from '../OlympicPlusGrid/OlimpicPlusGrid'
import { Match } from '../Match/Match'
import { openMatch } from '../../redux/singleTournamentService/singleTournamentSlice'

const TournamentStandingComponent = ({ stageNumber, auth }) => {
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const matchIsActive = sessionStorage.getItem('activatedComponent')
	const { tournamentUid } = useParams()
	const tournamentState = useSelector(selectTournament)

	const { data: stageData = {}, isLoading } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber: stageNumber
	}, { refetchOnMountOrArgChange: true }) // Stage data

	const [stage, setStage] = useState({})
	const [matchIsOpened, setMathIsOpened] = useState(matchIsActive ? true : false)
	const [matchData, setMatchData] = useState({})

	useEffect(() => {
		if (Object.keys(stageData).length !== 0) {
			const stageWithNamedGroups = assignAlphabeticalNames(stageData)
			setStage(stageWithNamedGroups)
		}
	}, [stageData, stageNumber, tournamentState])

	const sendMatchData = useCallback((matchData) => {
		setMatchData(matchData)
		setMathIsOpened(true)
		dispatch(openMatch(true))
	}, [dispatch])

	const closeMatch = useCallback(() => {
		setMathIsOpened(false)
		dispatch(openMatch(false))
	}, [dispatch])

	if (isLoading) { return <h1>Loading</h1> }

	return (
		<>
			<div className="standing-container">
				<div className="standing-wrapper">
					{
						!matchIsOpened ?
							stage?.levels?.map((level, levelIndex) => {
								return (
									<React.Fragment key={levelIndex}>
										{
											stage?.levels?.length > 1 &&
											<p className="standing-header">
												{t('Level') + ' ' + (levelIndex + 1)}
											</p>
										}

										{
											level?.groups?.map((grp, index) =>
												<div key={index}>
													{
														grp?.type === GROUP_TYPE_RR ?
															<RoundRobinGrid
																group={grp}
																groupIndex={index}
																sendMatchData={sendMatchData}
																levelIndex={levelIndex}
																auth={auth}
															/>
															:
															<OlympicPlusGrid
																group={grp}
																groupIndex={index}
																sendMatchData={sendMatchData}
																levelIndex={levelIndex}
																auth={auth}
															/>
													}
												</div>
											)
										}
									</React.Fragment>
								)
							})
							:
							<Match match={matchData} closeMatch={closeMatch} />
					}
				</div>
			</div>
		</>
	)
}

export const TournamentStanding = memo(TournamentStandingComponent)