import { createSelector } from '@reduxjs/toolkit'

export const selectTournament = state => state.tournament.tournament
export const selectStage = state => state.stage.stage
export const selectParticipants = state => state.participants.participants
export const selectPlayersList = state => state.stage.playersList
export const selectChosenPlayerInList = state => state.stage.chosenPlayerInList
export const selectDragState = state => state.stage.dragState
export const selectStageCreation = state => state.tournament.stageCreation
export const selectMatchIsActive = state => state.tournament.match
export const selectError = state => state.error.error
export const selectAuth = state => state.auth.authorized

export const selectData = createSelector(
	[
		selectTournament, selectStage, selectParticipants,
		selectPlayersList, selectChosenPlayerInList, selectDragState,
		selectStageCreation, selectMatchIsActive, selectError, selectAuth
	],
	(data) => {
		return data
	}
)