import './TournamentSchedule.css'
import React, { memo, useCallback, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import { useGetStageQuery } from '../../redux/stageService/stageApiSlice'
import { selectTournament } from '../../redux/selectors'
import { assignAlphabeticalNames } from '../../general/generalExternalFunctions'
import { TableMatches } from '../TableMatches/TableMatches.js'
import { Match } from '../Match/Match'
import { openMatch } from '../../redux/singleTournamentService/singleTournamentSlice'

const TournamentScheduleComponent = ({ stageNumber, auth }) => {
	const dispatch = useDispatch()
	const { tournamentUid } = useParams()
	const matchIsActive = sessionStorage.getItem('activatedComponent')
	const tournamentState = useSelector(selectTournament)

	const { data: stageData = {}, isLoading } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber: stageNumber
	}, { refetchOnMountOrArgChange: true }) // Stage data

	const [stage, setStage] = useState({})
	const [matchIsOpened, setMathIsOpened] = useState(matchIsActive ? true : false)
	const [matchData, setMatchData] = useState({})

	useEffect(() => {
		processData(stageData)
	}, [stageData])

	const processData = (stage) => {
		if (Object.keys(stage).length !== 0) {
			const stageWithNamedGroups = assignAlphabeticalNames(stage)

			setStage(stageWithNamedGroups)
		}
	}

	const sendMatchData = useCallback((matchData) => {
		setMatchData(matchData)
		setMathIsOpened(true)
		dispatch(openMatch(true))
	}, [dispatch])

	const closeMatch = useCallback(() => {
		setMathIsOpened(false)
		dispatch(openMatch(false))
	}, [dispatch])

	if (isLoading) { return <h1>Loading</h1> }

	return (
		<div className="schedule__container">
			{
				!matchIsOpened ?
					<TableMatches
						data={stage}
						tournamentState={tournamentState}
						sendMatchData={sendMatchData}
						auth={auth}
					/>
					:
					<Match match={matchData} closeMatch={closeMatch} />
			}
		</div>
	)
}

export const TournamentSchedule = memo(TournamentScheduleComponent)