import './TournamentParticipants.css'
import { useState, useEffect, useCallback, memo } from 'react'
import { useParams } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'
import { useDispatch, useSelector } from 'react-redux'

import { selectParticipants, selectTournament } from '../../redux/selectors'

import { findPlayers, checkEquality } from '../../general/generalExternalFunctions'
import { TYPE_SINGLES, TYPE_DOUBLES } from '../../general/generalConstants'
import {
	useAddSinglePlayersMutation,
	useRemoveParticipantMutation,
	useAddDoublesMutation,
	useRemoveDoubleMutation,
	useSeedDoublesMutation,
	useSeedParticipantsMutation
} from '../../redux/tournamentParticipantsService/tournamentParticipantsApiSlice'
import { setParticipants } from '../../redux/tournamentParticipantsService/tournamentParticipantsSlice'
import { useGetPlayersQuery } from '../../redux/playersService/playersApiSlice'
import { ReactComponent as ButtonDelete } from '../../icon/minus-delete.svg'
import { Button } from '../../components/Button/Button'
import { Popup } from '../../components/Popup/Popup'
import { ButtonWhite } from '../../components/ButtonWhite/ButtonWhite'
import { DoublesearchForPlayers } from '../DoublesearchForPlayers/DoublesearchForPlayers'
import { SinglePlayersSearch } from '../SinglePlayersSearch/SinglePlayersSearch'
import GlobalStore from '../../GlobalStore'

import { checkParticipantsOrder, formArraysForRequests } from './externalFunctions'

const TournamentParticipantsComponent = ({ auth, tournamentRefetch }) => {
	const [addSinglePlayers] = useAddSinglePlayersMutation()
	const [removeParticipant] = useRemoveParticipantMutation()
	const [addDouble] = useAddDoublesMutation()
	const [removeDouble] = useRemoveDoubleMutation()
	const [seedSinglePlayers] = useSeedParticipantsMutation()
	const [seedDoubles] = useSeedDoublesMutation()
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { tournamentUid } = useParams()
	const { communityUid } = GlobalStore.getStore()
	const {
		players,
		doubles,
		type,
		stages_count,
		participants_count
	} = useSelector(selectTournament)
	const participants = useSelector(selectParticipants)
	const { data: communityPlayers = [] } = useGetPlayersQuery({ community_uid: communityUid }, { skip: !auth })

	const [searchPlayersList, setSearchPlayersList] = useState([])
	const [popupIsOpen, setPopupIsOpen] = useState(false)
	const [showButtonSaveList, setShowButtonSaveList] = useState(false)

	useEffect(() => {
		if (players || doubles) {
			dispatch(setParticipants({ participants: players || doubles, type }))
		}
	}, [dispatch, players, doubles, type])

	useEffect(() => {
		const isEqual = checkEquality(participants, players || doubles)

		if (isEqual) {
			setShowButtonSaveList(false)
		} else {
			setShowButtonSaveList(true)
		}
	}, [participants, players, doubles, dispatch, type])

	const showPopup = useCallback(() => {
		if (participants?.length > 7 || stages_count) {
			return
		}

		setSearchPlayersList(communityPlayers)
		setPopupIsOpen(true)

		document.body.style.overflow = 'hidden'
	}, [participants, stages_count, communityPlayers])

	const closePopup = useCallback(() => {
		setPopupIsOpen(false)

		document.body.style.overflow = 'auto'
	}, [])

	const search = useCallback((value) => {
		if (value !== '') {
			const foundPlayers = findPlayers(value, communityPlayers)

			setSearchPlayersList(foundPlayers)
		} else {
			setSearchPlayersList(communityPlayers)
		}
	}, [communityPlayers])

	const onDragEnd = (result) => {
		if (!auth || !result.destination) {
			return
		}

		const updatedParticipants = Array.from(participants)
		const [removed] = updatedParticipants.splice(result.source.index, 1)
		updatedParticipants.splice(result.destination.index, 0, removed)
		dispatch(setParticipants({ participants: updatedParticipants, type }))
	}

	const removeParticipantFromList = (participant) => {
		const updatedList = participants?.filter(p => participant?.uid !== p?.uid)
		dispatch(setParticipants({ participants: updatedList, type }))
	}

	const handleSaveListButton = async () => {
		const { added, removed } = formArraysForRequests(participants, players || doubles, type)
		let state = [...players || doubles]
		let updates = [...participants]

		if (added?.length > 0) {
			const newParticipants = await includeParticipant(added)

			if (type === TYPE_DOUBLES) {
				updates = updates?.map(el => {
					const found = newParticipants?.find(p => el?.uid !== p?.uid &&
						el?.player1?.uid === p?.player1_uid && el?.player2?.uid === p?.player2_uid)

					if (found) {
						const updatedParticipant = { ...el, uid: found?.uid }
						state?.push(updatedParticipant)

						return updatedParticipant
					} else {
						return el
					}
				})
			}
		}

		if (removed?.length > 0) {
			await excludeParticipant(removed)

			state = state?.filter(el => {
				return removed.find(item => el?.uid !== item)
			})
		}

		const seed = checkParticipantsOrder(updates, state, type)

		if (seed?.length > 0) {
			await saveSeed(seed)
		}

		tournamentRefetch()
	}

	const includeParticipant = async (newPlayers) => {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'players': newPlayers
				}

				response = await addSinglePlayers({ tournament_uid: tournamentUid, body: body }).unwrap()

			} else if (type === TYPE_DOUBLES) {
				body = {
					TYPE_DOUBLES: newPlayers
				}

				response = await addDouble({ tournament_uid: tournamentUid, body: body }).unwrap()
			}

			if (response?.error) {
				console.log('Include participant return error', response?.error)

				return
			}

			return response
		} catch (e) {
			console.log('Include participant catch error', e)
		}
	}

	const excludeParticipant = async (removedPlayers) => {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'players': removedPlayers
				}

				response = await removeParticipant({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			} else if (type === TYPE_DOUBLES) {
				body = {
					TYPE_DOUBLES: removedPlayers
				}

				response = await removeDouble({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			}

			if (response?.error) {
				console.log('Exclude participant return error', response?.error)

				return
			}

			return response
		} catch (e) {
			console.log('Exclude participant catch error', e)
		}
	}

	const saveSeed = async (seed) => {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'players': seed
				}

				response = await seedSinglePlayers({ tournament_uid: tournamentUid, body: body }).unwrap()

			} else if (type === TYPE_DOUBLES) {
				body = {
					TYPE_DOUBLES: seed
				}

				response = await seedDoubles({ tournament_uid: tournamentUid, body: body }).unwrap()
			}

			if (response?.error) {
				console.log('Seed participant return error', response?.error)

				return
			}

			return response?.doubles
		} catch (e) {
			console.log('Seed participant catch error', e)
		}
	}

	return (
		<>
			<div
				className="tournament__participants-section"
				style={{
					marginBottom: showButtonSaveList ?
						'140px'
						:
						'80px'
				}}
			>
				<div className="tournament__participants-header">
					<p className="tournament__participants-title">
						{t('Saved')} {`(${participants_count})`}
					</p>

					{
						popupIsOpen && auth &&
						<Popup title={'Add participant'} onClose={() => closePopup(false)} close={true}>
							{
								type === TYPE_SINGLES &&
								<SinglePlayersSearch
									search={search}
									searchPlayersList={searchPlayersList}
									participants={participants}
									type={type}
								/>
							}

							{
								type === TYPE_DOUBLES &&
								<DoublesearchForPlayers
									search={search}
									searchPlayersList={searchPlayersList}
									participants={participants}
									type={type}
								/>
							}
						</Popup>
					}
				</div>

				{
					auth &&
					<div className="participants__button-addition">
						<ButtonWhite
							title={t('Add participant')}
							onClick={showPopup}
							disabled={participants?.length > 7 || stages_count > 0 ? true : false}
						/>
					</div>
				}

				<div className="participants">
					<DragDropContext onDragEnd={onDragEnd}>
						<Droppable droppableId="participants--singles">
							{
								(provided) => (
									<div
										className="participants--singles"
										{...provided.droppableProps}
										ref={provided.innerRef}
									>
										{
											participants?.map((el, index) =>
												<Draggable
													key={el?.uid}
													draggableId={el?.uid}
													index={index}
													isDragDisabled={stages_count > 0 ? true : false}
												>
													{
														(provided, snapshot) => (
															<div
																className="participants-item"
																ref={provided.innerRef}
																{...provided.draggableProps}
																{...provided.dragHandleProps}
																style={{
																	userSelect: 'none',
																	backgroundColor: snapshot.isDragging ?
																		'#F5FAFF'
																		:
																		'transparent',
																	...provided.draggableProps.style
																}}
															>
																{
																	type === TYPE_SINGLES ?
																		<>
																			<div className="participants__raiting">
																				<p className="participants__number">
																					{index + 1}
																				</p>
																			</div>

																			<p className="participants__name">
																				{`${el?.formedName}`}
																			</p>

																			{
																				auth && stages_count < 1 &&
																				<div>
																					<ButtonDelete
																						className="icon-deletion"
																						onClick={() => removeParticipantFromList(el)}
																					/>
																				</div>
																			}
																		</>
																		:
																		<>
																			<div className="participants__raiting">
																				<p className="participants__number">
																					{index + 1}
																				</p>
																			</div>

																			<div className="participants__names">
																				<p className="participants__palyer--first">
																					{el?.player1?.formedName}
																				</p>

																				<p className="participants__palyer--second">
																					{el?.player2?.formedName}
																				</p>
																			</div>

																			{
																				auth && stages_count < 1 &&
																				<div className="participants-icons">
																					<ButtonDelete
																						className="icon-deletion"
																						onClick={() => removeParticipantFromList(el)}
																					/>
																				</div>
																			}
																		</>
																}
															</div>
														)
													}
												</Draggable>
											)
										}

										{provided.placeholder}
									</div>
								)
							}
						</Droppable>
					</DragDropContext>
				</div>

				<div className="participants__button-save">
					{
						showButtonSaveList &&
						<Button onClick={handleSaveListButton} title={t('Save')} />
					}
				</div>
			</div>
		</>
	)
}

export const TournamentParticipants = memo(TournamentParticipantsComponent)