import './Tournament.css'
import { useState, useEffect, useMemo, useCallback } from 'react'
import { useParams, useLocation, useNavigate } from 'react-router-dom'

import { useDispatch, useSelector } from 'react-redux'

import { useGetTournamentQuery } from '../../redux/singleTournamentService/singleTournamentApiSlice'
import { useGetCommunityQuery } from '../../redux/communityService/communityApiSlice'
import { selectMatchIsActive, selectStageCreation } from '../../redux/selectors'
import { createStage, setTournamentData } from '../../redux/singleTournamentService/singleTournamentSlice'
import { Header } from '../../components/Header/Header.js'
import { TournamentParticipants } from '../../components/TournamentParticipants/TournamentParticipants'
import { TournamentInfo } from '../../components/TournamentInfo/TournamentInfo'
import { TournamentStructure } from '../../components/TournamentStructure/TournamentStructure'
import { TournamentStanding } from '../../components/TournamentStanding/TournamentStanding'
import { TournamentSchedule } from '../../components/TournamentSchedule/TournamentSchedule'
import { TournamentResult } from '../../components/TournamentResult/TournamentResult'
import { TournamentMenu } from '../../components/TournamentMenu/TournamentMenu'
import NoContent from '../../components/NoContent/NoContent'
import GlobalStore from '../../GlobalStore'

import {
	checkTournamentContent,
	setCategoryOfTournament as getCategoryOfTournament
} from './externalFunctions'

const Tournament = () => {
	const location = useLocation()
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const STAGE_ORDER_KEY = sessionStorage.getItem('stageOrder')
	const MENU_TAB_NUMBER_KEY = sessionStorage.getItem('menuTabNumber')
	const { tournamentUid, guest } = useParams()
	const stageCreation = useSelector(selectStageCreation)
	const matchIsActive = useSelector(selectMatchIsActive)
	const { authorized, communityUid, guestCode } = GlobalStore.getStore()

	const [menuTabOrder, setMenuTabOrder] = useState(
		MENU_TAB_NUMBER_KEY && MENU_TAB_NUMBER_KEY !== '7' ? JSON.parse(MENU_TAB_NUMBER_KEY) : 1
	)
	const [stageTabOrder, setStageTabOrder] = useState(STAGE_ORDER_KEY ? JSON.parse(STAGE_ORDER_KEY) : 1)
	const [codeForGuest, setCodeForGuest] = useState(guestCode)

	const { data: tournament = {}, isLoading, refetch } = useGetTournamentQuery(
		{ tournament_uid: tournamentUid }, { skip: (guest && !codeForGuest) || (!authorized && !guest) }
	)

	const { data: community = {} } = useGetCommunityQuery(
		{ community_uid: communityUid },
		{ skip: Object.keys(tournament).length === 0 || guest || !authorized || !communityUid }
	)

	const auth = useMemo(() => {
		if (!guest && authorized) {
			return true
		} else {
			return false
		}
	}, [guest, authorized])

	useEffect(() => {
		if (guest) {
			GlobalStore.setItem('guestCode', guest)
			setCodeForGuest(guest)
		} else if (!guest) {
			GlobalStore.removeItem('guestCode')
			setCodeForGuest(null)
		}
	}, [guest])

	useEffect(() => {
		if (Object.keys(tournament).length !== 0) {
			dispatch(setTournamentData(tournament))

			if (STAGE_ORDER_KEY) {
				setStageTabOrder(JSON.parse(STAGE_ORDER_KEY))
			} else {
				setStageTabOrder(1)
			}
		}
	}, [tournament, STAGE_ORDER_KEY, dispatch, tournamentUid])

	const stages = useMemo(() => {
		return Array.from({ length: tournament?.stages_count }, (_, index) => index + 1)
	}, [tournament])

	const showNoContent = useMemo(() => {
		return  checkTournamentContent(tournament, menuTabOrder)
	}, [tournament, menuTabOrder])

	const tournamentCategory = useMemo(() => {
		if (community && Object.keys(community).length !== 0) {
			return getCategoryOfTournament(community, tournament)
		} else {
			return tournament?.category?.name
		}
	}, [community, tournament])

	const handleMenuTab = useCallback((number) => {
		if (stageCreation) {
			dispatch(createStage(false))
			sessionStorage.removeItem('activatedComponent')
		}

		if (number === 7) {
			GlobalStore.removeItem('guestCode')
			navigate('/')
		}

		setMenuTabOrder(number)
		sessionStorage.setItem('menuTabNumber', number)
	}, [stageCreation, dispatch, navigate])

	const handleStageTab = useCallback((number) => {
		setStageTabOrder(number)
		dispatch(createStage(false))
		sessionStorage.setItem('stageOrder', number)
		sessionStorage.removeItem('activatedComponent')
	}, [dispatch])

	const tournamentRefetch = () => {
		refetch()
	}

	if (isLoading || Object.keys(tournament).length === 0) { return <h1>Loading</h1> }

	return (
		<>
			<Header
				className="header-container"
				location={location}
				title={`${tournament?.name} ${tournamentCategory ? `(${tournamentCategory})` : ''}`}
			/>

			<div className="tournament-wrapper">
				<div className="tournament-container">
					<div className="tournament__components">
						{
							menuTabOrder === 1 && stages?.length > 0 && !showNoContent &&
							<TournamentStanding
								stageNumber={stageTabOrder}
								auth={auth}
							/>
						}

						{
							menuTabOrder === 2 && stages?.length > 0 && !showNoContent &&
							<TournamentSchedule
								stageNumber={stageTabOrder}
								auth={auth}
							/>
						}

						{
							menuTabOrder === 3 && !showNoContent &&
							<TournamentStructure
								stageNumber={stageTabOrder}
								auth={auth}
								tournamentRefetch={tournamentRefetch}
							/>
						}

						{
							menuTabOrder < 4 && showNoContent &&
							<NoContent menuTabOrder={menuTabOrder} tournament={tournament} auth={auth} />
						}

						{
							menuTabOrder === 4 &&
							<TournamentParticipants
								auth={auth}
								tournamentRefetch={tournamentRefetch}
							/>
						}

						{
							menuTabOrder === 5 && !stageCreation &&
							<TournamentInfo
								auth={auth}
								community={community}
							/>
						}

						{
							menuTabOrder === 6 && !stageCreation &&
							<TournamentResult
								community={community}
							/>
						}
					</div>
				</div>

				{
					!matchIsActive &&
					<TournamentMenu
						stages={stages}
						menuTabOrder={menuTabOrder}
						handleStageTab={handleStageTab}
						stageTabOrder={stageTabOrder}
						handleMenuTab={handleMenuTab}
					/>
				}
			</div>
		</>
	)
}

export default Tournament