import './OlimpicPlusGrid.css'
import { useMemo, useCallback } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'

import { selectTournament } from '../../redux/selectors'
import { TYPE_SINGLES, FINAL_COLUMN_NAME, RESULT_COLUMN_NAME } from '../../general/generalConstants'

import { formGrid } from './externalFunctions'

const OlimpicPlusGrid = ({
	sendMatchData,
	group,
	auth
}) => {
	const { t } = useTranslation()
	const { type, players, doubles } = useSelector(selectTournament)

	const grid = useMemo(() => {
		return formGrid(group)
	}, [group])

	const participantsMap = useMemo(() => {
		return new Map((players || doubles)?.map(item =>
			[item.uid, item]
		))
	}, [players, doubles])

	const formMatch = (match) => {
		const { matchData, nodeOne, nodeTwo } = match
		const uidOne = nodeOne?.player_uid || nodeOne?.double_uid
		const uidTwo = nodeTwo?.player_uid || nodeTwo?.double_uid
		let updatedMatch

		if (type === TYPE_SINGLES) {
			const singleOne = participantsMap.get(uidOne)?.formedName
			const singleTwo = participantsMap.get(uidTwo)?.formedName

			updatedMatch = {
				...matchData,
				side1: {
					namePlayer: singleOne,
					nodeUid: matchData.side1_uid
				},
				side2: {
					namePlayer: singleTwo,
					nodeUid: matchData.side2_uid
				},
				uid: matchData?.uid
			}
		} else {
			const doubleOne = {
				firstPlayer: participantsMap.get(uidOne)?.player1.formedName,
				secondPlayer: participantsMap.get(uidOne)?.player2.formedName
			}
			const doubleTwo = {
				firstPlayer: participantsMap.get(uidTwo)?.player1.formedName,
				secondPlayer: participantsMap.get(uidTwo)?.player2.formedName
			}

			updatedMatch = {
				...matchData,
				side1: {
					namePlayer1: doubleOne.firstPlayer,
					namePlayer2: doubleOne.secondPlayer,
					nodeUid: matchData.side1_uid
				},
				side2: {
					namePlayer1: doubleTwo.firstPlayer,
					namePlayer2: doubleTwo.secondPlayer,
					nodeUid: matchData.side2_uid
				},
				uid: matchData?.uid
			}
		}
		return updatedMatch
	}

	const goToMatch = (cell) => {
		const { matchData, nodeOne, nodeTwo } = cell
		const matchExists = !matchData || (nodeOne?.player_uid && nodeTwo?.player_uid) ||
			(nodeOne?.double_uid && nodeTwo?.double_uid)

		if (!matchExists || !auth) {
			return
		}

		const updatedMatch = formMatch(cell)

		sessionStorage.setItem('activatedComponent', 'match')
		sessionStorage.setItem('match', JSON.stringify(updatedMatch))
		sendMatchData(updatedMatch)
	}

	const getMatch = (match) => {
		const { nodeOne, nodeTwo } = match
		const uidOne = nodeOne?.player_uid || nodeOne?.double_uid
		const uidTwo = nodeTwo?.player_uid || nodeTwo?.double_uid
		const insteadOfNameOne = nodeOne?.name || '---'
		const insteadOfNameTwo = nodeTwo?.name || '---'

		if (type === TYPE_SINGLES) {
			const singleOne = participantsMap.get(uidOne)?.formedName
			const singleTwo = participantsMap.get(uidTwo)?.formedName

			return (
				<div className="olympic-grid__match-sides">
					<p className="olympic-grid__match-participant">{singleOne || insteadOfNameOne}</p>
					<p className="olympic-grid__match-participant">{singleTwo || insteadOfNameTwo}</p>
				</div>
			)
		} else {
			const doubleOne = {
				firstPlayer: participantsMap.get(uidOne)?.player1.formedName,
				secondPlayer: participantsMap.get(uidOne)?.player2.formedName
			}
			const doubleTwo = {
				firstPlayer: participantsMap.get(uidTwo)?.player1.formedName,
				secondPlayer: participantsMap.get(uidTwo)?.player2.formedName
			}

			return (
				<div className="olympic-grid__match-sides">
					<div className="olympic-grid__match-dobles">
						{
							uidOne ?
								<>
									<p className="olympic-grid__match-double-player">{doubleOne.firstPlayer}</p>
									<p className="olympic-grid__match-double-player">{doubleOne.secondPlayer}</p>
								</>
								: <p className="olympic-grid__match-noplayer">{insteadOfNameOne}</p>
						}
					</div>

					<div className="olympic-grid__match-dobles">
						{
							uidTwo ?
								<>
									<p className="olympic-grid__match-double-player">{doubleTwo.firstPlayer}</p>
									<p className="olympic-grid__match-double-player">{doubleTwo.secondPlayer}</p>
								</>
								: <p className="olympic-grid__match-noplayer">{insteadOfNameTwo}</p>
						}
					</div>
				</div>
			)
		}
	}

	const getSets = (match) => {
		const matchData = match?.matchData
		if (matchData?.sets.length > 0) {
			const sets = matchData?.sets
			return (
				<>
					{
						sets.map((set, index) => {
							return (
								<div key={index} className="olympic-grid__match-section-sets-set">
									<p
										className="olympic-grid__match-section-sets-set-score"
									>
										{set.score1}
										<sup className="olympic-grid__match-section-sets-set-tiebreak">
											{set.tie_break_score1}
										</sup>
									</p>
									<p
										className="olympic-grid__match-section-sets-set-score"
									>
										{set.score2}
										<sup className="olympic-grid__match-section-sets-set-tiebreak">
											{set.tie_break_score2}
										</sup>
									</p>
								</div>
							)
						})
					}
				</>
			)
		}
	}

	const getParticipantForResult = (node) => {
		const uid = node?.player_uid || node?.double_uid
		const insteadOfName = node?.name || '---'
		const participant = participantsMap.get(uid)

		if (type === TYPE_SINGLES) {
			return (
				<>
					{
						uid ?
							<p className="olympic-grid__result-name">{participant.formedName}</p>
							:
							<p className="olympic-grid__result-name">{insteadOfName}</p>
					}
				</>
			)
		} else {
			return (
				<>
					{
						uid ?
							<div className="olympic-grid__result-double">
								<p className="olympic-grid__result-name">{participant.player1.formedName}</p>
								<p className="olympic-grid__result-name">{participant.player2.formedName}</p>
							</div>
							:
							<p className="olympic-grid__result-name">{insteadOfName}</p>
					}
				</>
			)
		}
	}

	const matchStyle = (column, columnIndex, cell, cellIndex) => {
		const limit = column.roundName

		let style = {
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'space-between',
			borderBottom: '1px solid #ccc',
			paddingRight: 10 + 'px',
			cursor: 'pointer'
		}

		if (columnIndex > 0 && column.roundName !== RESULT_COLUMN_NAME) {
			style = {
				...style,
				backgroundColor: cell?.winner ? 'white' : '#EBF4FF'
			}
			if ((cellIndex + 1) % limit === 0) {
				style = {
					...style,
					borderBottom: '1px solid rgb(54, 123, 207)'
				}
			}
		}

		return style
	}

	const resultBlockStyle = useCallback((cellIndex) => {
		const heightOfSingle = '4.5vh'
		const heightOfDouble = '6vh'

		return {
			height: TYPE_SINGLES === type ? heightOfSingle : heightOfDouble,
			padding: TYPE_SINGLES === type ? null : '2.5px 0',
			display: 'flex',
			alignItems: 'center',
			flexDirection: 'row',
			backgroundColor: (cellIndex + 1) % 2 === 0 ? '#EBF4FF' : 'white',
			borderBottom: (cellIndex + 1) % 2 === 0 ? '1px solid #ccc' : 'none',
			whiteSpace: 'nowrap',
			textOverflow: 'ellipsis',
			overflow: 'hidden'
		}
	}, [type])

	const getClassOfRoundBlock = (cell) => {
		const { matchData, nodeOne, nodeTwo } = cell
		const matchIsPending = (nodeOne?.player_uid && nodeTwo?.player_uid) ||
			(nodeOne?.double_uid && nodeTwo?.double_uid)

		if (matchData) {
			if (matchData.is_finished) {
				return 'olympic-grid__match-section-round--inactive'
			} else if (!matchData.is_finished && matchData.sets.length > 0) {
				return 'olympic-grid__match-section-round--active'
			} else if (matchIsPending) {
				return 'olympic-grid__match-section-round--inactive'
			} else {
				return 'olympic-grid__match-section-round--disabled'
			}
		} else {
			return ''
		}
	}

	return (
		<div className="olympicplus-grid__container">
			<p className="olympicplus-grid__title">
				{t('Group')} {group.name}
			</p>

			<div style={{ display: 'flex' }}>
				{
					grid.map((column, columnIndex) => (
						<div key={columnIndex} className="olympicplus-grid__column">
							<h5
								className="olympicplus-grid__column-title">
								{column.roundName !== RESULT_COLUMN_NAME ?
									column.roundName > 1 ?
										`1/${column.roundName}`
										: t(FINAL_COLUMN_NAME)
									: t(column.roundName)}
							</h5>

							<div>
								{
									column.matches.map((cell, cellIndex) => (
										<div
											key={cellIndex}
											className="olympicplus-grid__cell"
											match={column.roundName !== RESULT_COLUMN_NAME ? 1 : 0}
										>
											{column.roundName !== RESULT_COLUMN_NAME ?
												<div
													className="olympic-grid__match"
													style={matchStyle(column, columnIndex, cell, cellIndex)}
													onClick={() => goToMatch(cell)}
												>
													<div className="olympic-grid__match-section-participant">
														<p className="olympic-grid__match-number">
															{cell?.matchNumber}
														</p>
														{getMatch(cell)}
													</div>

													<div className="olympic-grid__match-section-sets">
														{getSets(cell)}

														<div className={
															!cell?.matchData?.is_finished
																&& cell?.matchData?.sets.length ?
																'olympic-grid__match-section-round-border'
																: ''
														}
														>
															<div className={getClassOfRoundBlock(cell)}></div>
														</div>
													</div>
												</div>
												:
												<div
													className="olympic-grid__result"
													style={resultBlockStyle(cellIndex)}
												>
													<p className="olympic-grid__result-order">{cellIndex + 1}</p>
													{getParticipantForResult(cell)}
												</div>
											}
										</div>
									))
								}
							</div>
						</div>
					))
				}
			</div>
		</div>
	)
}

export default OlimpicPlusGrid