import './PlayerForm.css'
import { useTranslation } from 'react-i18next'
import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'

import { useAddPlayerMutation, useUpdatePlayerMutation } from '../../redux/playersService/playersApiSlice'
import { Header } from '../../components/Header/Header'
import { Input } from '../../components/Input/Input'
import { Button } from '../../components/Button/Button'
import { BottomMenu } from '../../components/BottomMenu/BottomMenu'

import { isValidEmail } from './externalFunctions'

const PlayerCreation = () => {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const updatedPlayer = JSON.parse(sessionStorage.getItem('updatedPlayer'))
	const [createPlayer] = useAddPlayerMutation()
	const [updatePlayer] = useUpdatePlayerMutation()
	const communityUid = localStorage.getItem('communityUid')

	const [gender, setGender] = useState(updatedPlayer?.gender ? updatedPlayer?.gender : null)
	const [firstName, setFirstName] = useState(updatedPlayer?.first_name ? updatedPlayer?.first_name : '')
	const [lastName, setLastName] = useState(updatedPlayer?.last_name ? updatedPlayer?.last_name : '')
	const [middleName, setMiddle] = useState(updatedPlayer?.middle_name ? updatedPlayer?.middle_name : '')
	const [birthDate, setBirthDate] = useState(updatedPlayer?.birth_date ? updatedPlayer?.birth_date : '')
	const [birthYear, setBirthYear] = useState(updatedPlayer?.birth_year ? updatedPlayer?.birth_year : '')
	const [thereDate, setThereDate] = useState(false)
	const [email, setEmail] = useState(updatedPlayer?.email ? updatedPlayer?.email : '')
	const [emailValid, setEmailValid] = useState('')
	const [phone, setPhone] = useState(updatedPlayer?.mobile ? updatedPlayer?.mobile : '')
	const [phoneValid, setPhoneValid] = useState('')

	const [firstNameErr, setFirstNameErr] = useState(false)
	const [lastNameErr, setLastNameErr] = useState(false)
	const [phoneErr, setPhoneErr] = useState(false)
	const [emailErr, setEmailErr] = useState(false)
	const [birthDateErr, setBirthDateErr] = useState(false)
	const [birthYearErr, setBirthYearErr] = useState(false)

	// Валидируем Input фамилии
	const validateLastName = (lastNameValue) => {
		if (lastNameValue.length > 0) {
			setLastNameErr(false)
			setLastName(lastNameValue)
		} else {
			setLastNameErr(true)
			setLastName('')
		}
	}

	// Валидируем Input имени
	const validateFirstName = (firstNameValue) => {
		if (firstNameValue.length > 0) {
			setFirstNameErr(false)
			setFirstName(firstNameValue)
		} else {
			setFirstNameErr(true)
			setFirstName('')
		}
	}

	// Валидируем Input даты рождения
	const validateBirthDate = (birthDateValue) => {
		console.log(birthDateValue)
		setBirthDate(birthDateValue)

		const parts = birthDateValue.split('-')
		const year = parts[0]
		if (parts?.length > 1) {
			setBirthDateErr(false)

			setBirthYear(year)

			setThereDate(true)
		}
		else {
			setBirthDateErr(true)
			setThereDate(false)
			setBirthDate('')
			setBirthYear('')
		}
	}

	// Валидируем Input год рождения
	const validateBirthYear = (birthYearValue) => {
		setBirthYear(birthYearValue)

		if (birthYearValue?.length > 0) {
			if (birthDate || birthYearValue) {
				if (birthYearValue?.length > 4) {
					setBirthYearErr(true)
				} else if (birthYearValue?.length < 4) {
					setBirthYearErr(true)
				} else {
					setBirthYear(birthYearValue)
					setBirthYearErr(false)
				}
			}
		} else {
			setBirthYearErr(false)
		}
	}

	// Валидируем Input Email
	const validateEmail = (emailValue) => {
		setEmail(emailValue)

		if (emailValue?.length > 0) {
			if (isValidEmail(email)) {
				setEmailErr(false)
				setEmailValid(emailValue)
			} else {
				setEmailErr(true)
				setEmailValid('')
			}
		} else {
			setEmailErr(false)
		}
	}

	const validatePhone = (phoneValue) => {
		const phoneNumberRegex = /^(?:\+7)?(\d{10})$/

		if (phoneValue?.length > 0) {
			if (phoneNumberRegex.test(phoneValue)) {
				if (phoneValue.startsWith('8')) {
					phoneValue = `+7${phoneValue.slice(1)}`
				}
				else if (phoneValue.startsWith('9')) {
					phoneValue = `+7${phoneValue}`
				}
				else if (phoneValue.startsWith('7')) {
					phoneValue = `+${phoneValue}`
				}

				setPhone(phoneValue)
				setPhoneValid(phoneValue)
				setPhoneErr(false)
			} else {
				setPhoneValid('')
				setPhoneErr(true)
			}

			setPhone(phoneValue)
		} else {
			setPhone(phoneValue)
			setPhoneValid(phoneValue)
			setPhoneErr(false)
		}
	}

	const handleSubmit = async (e) => {
		e.preventDefault()

		// Проверяем, что lastName и firstName заполнены
		if (!lastName || !firstName) {
			setFirstNameErr(true)
			setLastNameErr(true)
			// Если хотя бы одно из полей пустое, выходим из функции
			return
		} else {
			setFirstNameErr(false)
			setLastNameErr(false)
		}
		
		try {
			const body = {
				'gender': gender ? gender : null,
				'last_name': lastName,
				'first_name': firstName,
				'middle_name': middleName,
				'birth_date': birthDate ? birthDate : null,
				'birth_year': birthYear ? birthYear : null,
				'email': emailValid,
				'mobile': phoneValid,
				'community_uids': [communityUid]
			}
		
			let response = null
			if (updatedPlayer) {
				response = await updatePlayer({ body, player_uid: updatedPlayer?.uid }).unwrap()
			} else {
				response = await createPlayer({ body }).unwrap()
			}
		
			if (response?.error?.status === 401) {
				navigate('/')
			} else if (response?.error) {
				return
			} else {
				navigate('/account/players')
			}
		} catch (e) {
			console.log('createPlayer catch error', e)
		}
	}

	return (
		<>
			<Header
				title={updatedPlayer ? t('Edition') : t('Add player')}
				backButton={true}
			/>

			<div className="player-form__container">
				<form onSubmit={handleSubmit} noValidate>
					<div className="player-form__input">
						<label className="player-form__input-label">
							{t('Last name')}
						</label>

						<Input
							value={lastName}
							onChange={(e) => validateLastName(e.target.value)}
							type="text"
							required={false}
							borderColor={lastNameErr ? '#F20505' : ''}
						/>

						{
							lastNameErr &&
								<p className="player-form__error">
									{t('Enter last name')}
								</p>
						}
					</div>

					<div className="player-form__input">
						<label className="player-form__input-label">
							{t('Player name')}
						</label>

						<Input
							value={firstName}
							onChange={(e) => validateFirstName(e.target.value)}
							type="text"
							required={false}
							borderColor={firstNameErr ? '#F20505' : ''}
						/>

						{
							firstNameErr &&
								<p className="player-form__error">
									{t('Enter player name')}
								</p>
						}
					</div>

					<div className="player-form__input">
						<label className="player-form__input-label">
							{t('Middle name')}
						</label>

						<Input
							value={middleName}
							onChange={(e) => setMiddle(e.target.value)}
							required={false}
							type="text"
						/>
					</div>

					<div className="player-form__inputs-radio">
						<p className="inputs-radio__title">
							{t('Gender')}
						</p>

						<div className="inputs-radio__container">
							<input
								id="player-gender__male"
								name="gender"
								type="radio"
								value={'male'}
								onChange={(e) => setGender(e.target.value)}
								checked={gender === 'male'}
								className="inputs-radio__button"
							/>

							<label htmlFor="player-gender__male">
								{t('Male')}
							</label>
						</div>

						<div className="inputs-radio__container">
							<input
								id="player-gender__female"
								name="gender"
								type="radio"
								value={'female'}
								checked={gender === 'female'}
								onChange={(e) => setGender(e.target.value)}
								className="inputs-radio__button"
							/>

							<label htmlFor="player-gender__female">
								{t('Female')}
							</label>
						</div>
					</div>

					<div className="player-form__input">
						<label className="player-form__input-label">
							{t('Birth date')}
						</label>

						<Input
							value={birthDate}
							onChange={(e) => validateBirthDate(e.target.value)}
							type={'date'}
							required={false}
							borderColor={birthDateErr ? '#F20505' : ''}
						/>

						{
							birthDateErr &&
								<p className="player-form__error">
									{t('Incorrect birth date')}
								</p>
						}
					</div>

					<div className="player-form__input">
						<label className="player-form__input-label">
							{t('Birth year')}
						</label>

						<Input
							value={birthYear}
							onChange={(e) => validateBirthYear(e.target.value)}
							type="number"
							required={false}
							borderColor={birthYearErr ? '#F20505' : ''}
							readOnly={thereDate}
						/>

						{
							birthYearErr &&
								<p className="player-form__error">
									{t('Incorrect birth year')}
								</p>
						}
					</div>

					<div className="player-form__input">
						<label className="player-form__input-label">
							{t('Email')}
						</label>

						<Input
							value={email}
							onChange={(e) => validateEmail(e.target.value)}
							type={'email'}
							borderColor={emailErr ? '#F20505' : ''}
							required={false}
						/>

						{
							emailErr &&
								<p className="player-form__error">
									{t('Invalid email')}
								</p>
						}
					</div>

					<div className="player-form__input">
						<label htmlFor="phone" className="player-form__input-label">
							{t('Phone number')}
						</label>

						<Input
							value={phone}
							onChange={(e) => validatePhone(e.target.value)}
							type="text"
							id="phone"
							borderColor={phoneErr ? '#F20505' : ''}
							required={false}
						/>

						{
							phone && phoneErr &&
								<p className="player-form__error">
									{t('Invalid phone number')}
								</p>
						}
					</div>

					<div className="player-form__submit">
						{/* <Button type={'submit'} title={t('Save')} disabled={isButtonEnabled} /> */}
						<Button type={'submit'} title={t('Save')} />
					</div>
				</form>
			</div>

			<BottomMenu />
		</>
	)
}

export default PlayerCreation







































