import './TournamentStructure.css'
import { useSelector } from 'react-redux'
import { memo } from 'react'

import { StageForm } from '../StageForm/StageForm'
import { StageCreation } from '../StageCreation/StageCreation'
import { selectTournament, selectStageCreation } from '../../redux/selectors'

const TournamentStructureComponent = ({ stageNumber, tournamentRefetch }) => {
	const creation = sessionStorage.getItem('activatedComponent')
	const stageCreation = useSelector(selectStageCreation)
	const { stages_count } = useSelector(selectTournament)

	return (
		<>
			{
				stages_count > 0 && !stageCreation && !creation &&
				<StageForm
					stageNumber={stageNumber}
					tournamentRefetch={tournamentRefetch}
				/>
			}

			{
				(stages_count < 1 || stageCreation || creation) &&
				<StageCreation />
			}
		</>
	)
}

export const TournamentStructure = memo(TournamentStructureComponent)