export const checkTournamentContent = (tournament, number) => {
	const { players, doubles, stages_count } = tournament

	if ((players?.length < 1 || doubles?.length < 1) && number < 4) {
		return true
	} else if (stages_count < 1 && number < 3) {
		return true
	} else {
		return false
	}
}

export const setCategoryOfTournament = (community, tournament) => {
	const category = community?.categories?.find(c =>
		c?.uid === tournament?.category_uid
	)
	return category?.name
}