import './NoContent.css'
import React from 'react'
import { useTranslation } from 'react-i18next'

import { useSelector } from 'react-redux'

import { selectTournament } from '../../redux/selectors'

import PlayersBlue from '../../icon/players-blue.svg'
import StructureBlue from '../../icon/structure_menu_blue.svg'

const NoContent = ({ auth, menuTabOrder }) => {
	const { t } = useTranslation()
	const { players, doubles, stages_count } = useSelector(selectTournament)

	return (
		<>
			<div className="nocontent__container">
				{
					menuTabOrder < 3 &&
					<div className="nocontent__section">
						<p className="nocontent__title">
							{
								menuTabOrder === 1 ?
									t('There are no grids yet')
									:
									t('No matches yet')
							}
						</p>

						{
							(players?.length < 1 || doubles?.length < 1) && auth &&
							<div className="nocontent__paragraph">
								<img src={PlayersBlue} alt="Players" />

								<p className="nocontent__text">
									{t('Add players to the tournament in the participants tab')}
								</p>
							</div>
						}

						{
							stages_count < 1 && auth &&
							<div className="nocontent__paragraph">
								<img src={StructureBlue} alt="Structure" />

								<p className="nocontent__text">
									{t('Add a stage in the structure tab')}
								</p>
							</div>
						}
					</div>
				}

				{menuTabOrder === 3 &&
					<div className="nocontent__section">
						<p className="nocontent__title">
							{t('You can not create a stage yet')}
						</p>

						<div className="nocontent__paragraph">
							<img src={PlayersBlue} alt="Players" />
							<p className="nocontent__text">
								{t('Add players to the tournament in the participants tab')}
							</p>
						</div>
					</div>}
			</div>
		</>
	)
}

export default NoContent